import 'babel-polyfill'
import { sendRequest } from './utils'

async function send() {
  let json = await sendRequest( 'http://china.nba.com/static/data/league/historicalplayerlist_A_1946-1949.json' );
  console.log( json );
  return json;
}

send().then(function(json) {
  console.log('Contents: ' + json);
}, function(error) {
  console.error('出错了', error);
});
